.delete-button {
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.5);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: bold;
  position: absolute;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.75);
  border-radius: 50%;
  color: white;
  left: -10px;
  top: -10px;
  width: 20px;
  height: 20px;
  opacity: 0;
  transition: opacity 0.25s ease-in-out;
}

.movie:hover .delete-button,
.nominated-movie:hover .delete-button {
  opacity: 1;
}
