.nominate-button {
  align-items: center;
  justify-content: center;
  display: flex;
}

.nominate-button button {
  cursor: pointer;
  color: #fff;
  font-weight: 18px;
  font-size: 18px;
  border-radius: 3px;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.5);
  background-color: rgba(0, 0, 0, 0.9);
  height: 40px;
  width: 150px;
}

.nominate-button button:disabled {
  opacity: 0.6;
  cursor: default;
}
